'use strict';

$(function() {
    $(".hamburger-button").click(function() {
        if(this.classList.contains("active") === true) {
          $(".hamburger-button").removeClass("active");
          $(".header__nav").slideUp();
        } 
        else {
          $(this).toggleClass("active");
          $(".header__nav").slideDown() ;
        }
    })
});